.settingSidebar {
	background: #fff;
	position: fixed;
	height: 100%;
	width: 280px;
	top: 70px;
	right: -280px;
	z-index: 100;
	transition: 0.3s ease-in;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.16), 0 2px 10px rgba(0, 0, 0, 0.12);
	.settingPanelToggle {
		background: #6777ef;
		padding: 10px 15px;
		color: #fff;
		position: absolute;
		top: 40%;
		left: -40px;
		width: 40px;
		border-radius: 50px 0 0 50px;
	}
	&.showSettingPanel {
		right: 0;
	}
	.settingSidebar-body {
		position: relative;
		height: 100%;
	}
	.settingSidebar-tab {
		display: flex;
		.nav-item {
			width: 33.33%;
			text-align: center;
			.nav-link {
				padding: 15px 12px;
				color: #6a7a8c;
				border-bottom: 3px solid transparent;
				&.active {
					border-bottom: 3px solid #2962ff;
					color: #2962ff;
				}
				&:hover {
					border-bottom: 3px solid #2962ff;
					color: #2962ff;
				}
			}
		}
	}
	ul.choose-theme li {
	display: inline-block;
	&:hover {
		cursor: pointer;
	}
	
}

ul.choose-theme li div {
	border-radius: 4px;
    display: inline-block;
    vertical-align: middle;
    height: 25px;
    width: 25px;
    overflow: hidden;
    position: relative;
    border: 7px solid rgba(0, 0, 0, 0.3);
    margin:2px;
}

