.statistic-details {
  display: flex;
  flex-wrap: wrap;
  .statistic-details-item {
    flex: 1;
    padding: 17px 10px;
    text-align: center;
    .detail-chart {
      margin-bottom: 10px;
      padding: 0 20px;
    }
    .detail-name {
      font-size: 12px;
      margin-top: 0px;
      color: color(fontdark);
      letter-spacing: .3px;
    }
    .detail-value {
      font-size: 18px;
      font-weight: 700;
    }
    .card-widget-square{
  		width: 45px;
    	height: 45px;
    	line-height: 45px;
    	font-size: 20px;
    	margin: 10px 10px 10px 10px;
    	border-radius: 6px;
    	background: transparent;
  	}
  	.card-widget-color1{
  		border : 2px solid #4351f1;
  		color : #4351f1;
  	}
  	.card-widget-color2{
  		border : 2px solid #1d7074;
  		color : #1d7074;
  	}
  	.card-widget-color3{
  		border : 2px solid #04f5d4;
  		color : #04f5d4;
  	}
  }
}

@include media-breakpoint-down(xs) {
  .statistic-details {
    flex-wrap: wrap;
    .statistic-details-item {
      flex: initial;
      width: 50%;
    }
  }
}